






















import { Component } from "vue-property-decorator";
import BaseNode from "./BaseNode.vue";
import NodeClass from "./NodeClass";
import { FreeNodeData, getBaseNode, IBaseNode } from "@tefact/core";
import { PageProperties } from "../config";

@Component({
  components: { BaseNode },
})
export default class CardNode extends NodeClass<IBaseNode> {
  editor: any = null;

  static DEFAULT = {
    ...getBaseNode(),
    type: "CardNode",
    data: {
      title: "",
      content: "",
    } as FreeNodeData,
  } as IBaseNode;

  static NODE = {
    icon: "tf-icon-card",
    type: "CardNode",
    title: "",
    nodeData: CardNode.DEFAULT,
  };

  static PROPERTIES = PageProperties.defaultProperties("", [
    {
      type: "RowsProperties",
      params: {
        title: "",
        rows: [
          {
            type: "TextRow",
            params: {
              title: "",
              path: "data.title",
            },
          },
          {
            type: "TextAreaRow",
            params: {
              title: "",
              path: "data.content",
            },
          },
        ],
      },
    },
  ]);
}
