import { IPropertiesComponents } from "@tefact/properties"
import { IPropertiesRow, IPropertiesTab } from "@tefact/properties"
import { IFormNode } from "./types"

export class FormProperties {
  static defaultStyledProperties() {
    return {
      title: '',
      components: [
        {
          type: 'RowsProperties', params: {
            title: '',
            rows: [{
              type: 'TextRow',
              params: {
                title: "",
                path: "data.text"
              }
            }]
          }
        },
        { type: 'BackgroundProperties', params: {} },
        { type: 'BorderProperties', params: {} },
        { type: 'TextProperties', params: {} }
      ],
    }
  }

  static defaultProperties(type: string, components?: Array<IPropertiesComponents> | null, rowComponents?: Array<IPropertiesRow>) {
    const res = {
      title: '',
      components: [
        {
          type: 'RowsProperties',
          params: {
            title: '',
            rows: [{
              type: 'TextRow',
              params: {
                title: "",
                path: "label"
              }
            }, {
              type: type,
              params: {
                title: "",
                path: "default"
              }
            }, {
              type: 'TextRow',
              params: {
                title: "",
                path: "desc"
              }
            }, {
              type: 'TextRow',
              params: {
                title: "",
                path: "placeholder"
              }
            }, {
              type: 'SwitchRow',
              params: {
                title: "",
                path: "notNull"
              }
            }]
          }
        },
        { type: 'BackgroundProperties', params: {} },
        { type: 'BorderProperties', params: {} }
      ],
    } as IPropertiesTab

    if (components) {
      res.components = [ ...res.components, ...components ]
    }

    if (rowComponents) {
      const params = res.components[0].params
      if (params.rows) {
        params.rows = [ ...(params.rows as Array<IPropertiesRow>), ...rowComponents ]
      } else {
        params.rows = rowComponents;
      }
    }

    return res;
  }

  static PROPERTIES = {
    title: '',
    hasTab: true,
    tabs: [
      {
        title: '',
        components: [
          { type: 'BackgroundProperties', params: {} },
          { type: 'BorderProperties', params: {} },
          {
            type: 'RowsProperties', params: {
              title: '',
              rows: [{
                type: 'SelectRow',
                params: {
                  title: "",
                  path: "config.viewMode",
                  selectOptions: [
                    {value: 'fixed', label: ''},
                    {value: 'adapt', label: ''},
                  ]
                }
              }, {
                type: 'NumberRow',
                params: {
                  title: "",
                  unit: "px",
                  path: "config.style.width"
                }
              }, {
                type: 'NumberRow',
                params: {
                  title: "",
                  unit: "px",
                  path: "config.labelWidth"
                }
              }]
            }
          },
          {
            type: 'ListProperties',
            params: {
              title: '',
              path: 'config.buttons',
              rows: [{
                type: 'TextRow',
                params: {
                  title: "",
                  path: "label"
                }
              }, {
                type: 'SelectRow',
                params: {
                  title: "",
                  path: "type",
                  selectOptions: [
                    { value: 'submit', label: '' },
                    { value: 'clear', label: '' },
                    { value: 'link', label: '' },
                  ]
                }
              }, {
                type: 'SelectRow',
                params: {
                  title: "",
                  path: "styleType",
                  selectOptions: [
                    { value: 'primary', label: 'Primary' },
                    { value: 'info', label: 'Info' },
                    { value: 'warning', label: 'Warning' },
                    { value: 'danger', label: 'Danger' },
                    { value: 'text', label: 'Text' },
                    { value: 'success', label: 'Success' },
                    { value: 'default', label: '' },
                  ]
                }
              }, {
                type: 'TextRow',
                params: {
                  title: "",
                  path: "link"
                }
              }]
            }
          }
        ],
      },
    ],
  }

  static getBaseFormSection(type: string, config?: any, h?: number): IFormNode {
    return {
      id: "",
      style: {},
      pos: {
        h
      },
      label: '',
      key: 'fieldName',
      default: '',
      itemType: 'field',
      type: type,
      ...config
    }
  }
}
